import React from 'react'
import styles from './Home.module.css'
import RadioGroup from '../Radio/RadioGroup'
import Radio from '../Radio/Radio'
import Toggle from '../Toggle/Toggle'

export default function Home({
  isEngHide,
  radioOption,
  changeLangMode,
  changeListMode
}) {
  const handleChange = e => changeLangMode(e.target.checked)

  const handleChangeRadio = e => {
    if (e.target.checked) changeListMode(e.target.value)
  }

  return (
    <div className={['section', styles.home].join(' ')}>
      <div className={'container'}>
        <ul className={styles.list}>
          <li className={styles.item}>
            <span className={styles.title}>Скрыть слова:</span>
            <Toggle
              checked={isEngHide}
              onChange={handleChange}
              falseText='Rus'
              trueText='Eng'
            />
          </li>
          <li className={styles.item}>
            <span className={styles.title}>Обрезать список:</span>
            <RadioGroup>
              {['all', '50', '25'].map(text => (
                <Radio
                  value={text}
                  text={text}
                  key={text}
                  checked={radioOption}
                  onChange={handleChangeRadio}
                />
              ))}
            </RadioGroup>
          </li>
        </ul>
      </div>
    </div>
  )
}
